.about {
  padding: 120px 45px 120px 45px;


  @include vp-1199 {
    padding: 100px 45px 52px 45px;
  }

  @include vp-767 {
    padding: 70px 0 70px 0;
  }
}

.about__container {
  display: grid;
  //grid-template-columns: 32.3% 60%;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(3, auto);
  column-gap: 40px;
  justify-items: center;
  max-width: 1100px;
  margin-left: auto;
  //position: relative;

  @include vp-1199 {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    max-width: none;
    margin-left: 0;
    column-gap: 0;
  }

  @include vp-1199 {
    grid-template-rows: repeat(3, auto);
    row-gap: 18px;
  }
}

.about__info-wrapper {
  //justify-self: end;
  padding-top: 50px;
  grid-column: 1/6;
  grid-row: 1/-1;
  z-index: 1;
  //width: 32.3%;
  padding-right: 78px;
  //margin-right: 7.7%;

  @include vp-1199 {
    grid-column: 1/-1;
    grid-row: 1/2;
    padding-right: 0;
    padding-top: 0;
  }

  @include vp-767 {
    padding: 0 15px;
  }
}

.about h2 {
  font-family: $ff-bebasneue;
  //font-weight: 400;
  margin: 0;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  margin-bottom: 4px;
  text-transform: uppercase;
  //color: $color-default-sapphire;

  @include vp-1199 {
    font-size: 42px;
    line-height: 42px;
    text-align: center;
    margin-bottom: 8px;
    max-width: 502px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    text-align: left;
    margin-bottom: 4px;
    max-width: none;
  }
}

.about h2 + p {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 39px;

  @include vp-1199 {
    display: block;
    margin-bottom: 33px;
  }

  @include vp-767 {
    margin-bottom: 25px;
    padding-right: 0;
  }
}

.about p {
  margin: 0;
  padding: 0;
  margin-bottom: 8px;
  font-weight: 300;

  //@include vp-1199 {
  //  margin-bottom: 17px;
  //  line-height: 19px;
  //}
  //
  //@include vp-767 {
  //  font-size: 14px;
  //  line-height: 19px;
  //}

  @include vp-1199 {
    text-align: center;
    max-width: 502px;
    margin-bottom: 26px;

    &:nth-child(3n+1) {
      display: none;
    }
  }

  @include vp-767 {
    text-align: left;
    margin-bottom: 15px;
    padding-right: 15px;
    max-width: none;
  }
}



.about__image-container {
  //justify-self: end;

  height: 401px;
  align-self: start;
  grid-row: 1/-1;
  z-index: 1;
  //width: 60%;
  grid-column: 6/-1;
  margin-left: -35px;
  background-color: $color-spindle;

  @include vp-1199 {
    grid-column: 1/-1;
    grid-row: 2/4;
    margin-left: 0;
    height: 345px;
    width: 100%;
  }

  @include vp-767 {
    grid-row: 2/3;
    height: 313px;
    //height: 100%;
  }
}

.about img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;

  @include vp-1199 {
    object-position: center;
  }
}



