.main-nav {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;

  @include vp-1199 {
    margin-bottom: 24px;
  }
}

.main-nav__logo {
  width: 89px;
  height: 20px;
  display: flex;
  margin-right: 30px;
  margin-top: 15px;

  @include vp-1199 {
    width: 82px;
    height: 24px;
  }

  @include vp-767 {
    width: 68px;
    height: 15px;
  }
}

.main-nav__logo img {
  width: 89px;
  height: 20px;

  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 68px;
    height: 15px;
  }
}

.main-nav address {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  font-style: normal;
  justify-content: flex-start;
  margin-left: auto;

  //@include vp-1919 {
