  //}
}

.main-nav__link {
  display: flex;
  //padding-left: 35px;
  //padding-top: 16px;
  //position: relative;
  margin-left: 15px;
  margin-top: 15px;
  color: $color-default-white;
  font-family: $ff-bebasneue;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  //font-weight: 400;

  @include vp-1199 {
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }

  &:hover::before,
  &:hover {
    opacity: 0.8;
  }
}

